.cell{display:flex;flex-direction:column;align-items:flex-start;width:100%;color:inherit}.cell.-li{display:grid;grid-template-columns:repeat(2, 1fr);align-items:center;gap:0 1rem;padding:2rem 0;border-top:1px solid var(--border-color)}@media screen and (max-width: 768px){.cell.-li{grid-template-columns:100%;padding:1.5rem 0}.cell.-li .text.-label{margin-bottom:.5rem}}.cell.-tb{display:grid;grid-template-columns:2fr 1fr 1fr;align-items:center;gap:0 1rem;padding:2rem 0;border-top:1px solid var(--border-color)}@media screen and (max-width: 1440px){.cell.-tb{grid-template-columns:repeat(3, 1fr)}}@media screen and (max-width: 768px){.cell.-tb{grid-template-columns:100%;padding:1.5rem 0}.cell.-tb .text.-label{margin-bottom:.5rem}}.cell.-rw{flex-direction:row;align-items:center;gap:1rem}.cell.-ac{align-items:center;justify-content:center}.cell.-ac *{text-align:center}.cell.-gs{gap:.5rem}.cell.-gm{gap:1rem}.cell.-gl{gap:2rem}.cell.-mb{margin-bottom:1rem}