section{z-index:1;display:flex;flex-direction:column;gap:var(--margin-row);width:100%;padding:var(--margin-row) var(--margin);background:var(--background-color)}section#intro{display:grid;grid-template-columns:3.5fr 4fr .5fr;gap:2rem 1rem;padding-bottom:calc(var(--margin-row)*2)}section#intro h2{font-weight:var(--font-weight)}@media screen and (max-width: 1440px){section#intro{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 768px){section#intro{grid-template-columns:100%}}section#bio{padding-top:calc(var(--margin-row)*2)}section#info{display:flex;flex-direction:column}section#map{display:flex;flex-direction:column}section#footer{position:sticky;z-index:0;display:flex;flex-direction:column;align-items:center;justify-content:center;gap:var(--margin-row);top:0;left:0;bottom:0;height:100vh;color:var(--background-color);background:var(--foreground-color)}section#footer span{color:var(--background-color)}section#footer span:nth-of-type(1){font-size:12vw;font-weight:var(--display-weight);line-height:1;letter-spacing:var(--display-spacing)}section#footer span:nth-of-type(2){margin-top:1rem;margin-bottom:var(--margin-row)}