.button{display:flex;align-items:center;gap:.5rem;padding:.75rem;background:rgba(var(--foreground-rgb), 0.15);border-radius:.5rem;font-size:var(--subtext-size);font-weight:var(--subtext-weight);line-height:1;letter-spacing:var(--subtext-spacing)}.button img{width:1.25rem;height:1.25rem;border-radius:.25rem}.button:hover{color:var(--background-color);background:var(--foreground-color)}